}
.title_contacto{
    /* margin: 0; */
    font-size: 4.5rem;
    color: rgb(233, 225, 225);
    background-color: orange;
    /* border: 2px solid white; */
    border-radius: 15px;
    text-align: center;
    padding: 5%;
    letter-spacing: 2px;
    font-weight:600;
    font-family: 'Poppins', sans-serif;
    cursor: pointer;
}
.contacto_Img{
    /* background-color: rgba(46, 139, 86, 0.308); */
