    height: 80%;
    display: flex;
    justify-content: space-evenly;
}
.contacto{
    /* border: 2px solid salmon; */
    transition: all 4s;
    display: flex;
    align-items: center;
    justify-content: center;
}
.contacto:hover{
    display: none;
    transition: 4s;
}
.cont_img{
